.form {
    display: flex;
    flex-direction: column;

    align-items: center;
}
.form__wrapper {
    display: flex;
    flex-direction: column;

    min-width: 320px;

    align-items: center;
}

.form__title {
    font-size: 14px;
    font-weight: normal;
    line-height: 30px;

    width: 280px;
    padding-top: 45px;
    padding-bottom: 34px;

    text-align: center;

    color: @bg-lightblue;
}
.form__title-hidden {
    display: none;
}
.form__full-name {
    display: flex;
    flex-direction: column;

    width: 280px;
    margin: 0;
    padding: 0;
    padding-bottom: 42px;

    list-style: none;
}

.form__area-label {
    font-size: 14px;
    line-height: 30px;

    margin-left: 22px;

    text-transform: uppercase;

    color: @pink;
}
.form__area {
    font-size: 19px;

    width: 275px;
    min-height: 57px;
    margin-top: 5px;

    border: 2px solid rgb(229, 229, 229);

    &:hover {
        border: 2px solid @bg-lightblue;
    }
    &:active,
    &:focus {
        border: 2px solid @pink;
        outline: none;
    }
}

.form__area::placeholder {
    font-size: 18px;
    // padding-left: 20px;

    opacity: 1;
    color: @bg-lightblue;
}


.form__full-field {
    margin-top: 17px;
}
.form__full-field:first-of-type {
    margin-top: 0;
}

.form__achievements {
    width: 100%;
    padding-top: 38px;
    padding-bottom: 40px;

    background-color: @bg-grey;
}

.form__achievements-fieldset {
    width: 280px;
    margin: 0 auto;
    padding: 0;

    border: none;
}
.form__achievements-list {
    display: flex;
    flex-direction: column;

    width: 280px;
    margin: 0 auto;
    padding: 0;

    list-style: none;

    align-items: center;
}
.form__achievements-title {
    font-size: 14px;
    font-weight: normal;
    line-height: 18px;

    width: 230px;
    margin: 0 auto;
    padding-bottom: 42px;

    text-align: center;
    text-transform: uppercase;

    color: @pink;
}


.form__achievements-list input[type="checkbox"] {
    display: none;
}
.form__achievements-item {
    min-height: 38px;
    margin-top: 44px;

    align-self: flex-start;
}
.form__achievements-item:first-of-type {
    margin-top: 0;
}

.form__achievements-label {
    position: relative;

    margin: 0;
    padding-left: 70px;

    vertical-align: middle;

    color: @bg-lightblue;


    &::after {
        position: absolute;
        top: -6px;
        left: 0;

        width: 35px;
        height: 35px;

        content: "";

        border: 2px solid @border-color-gray;
        background-color: @bg-white;
    }
    &:hover::after {
        border: 2px solid @bg-lightblue;
    }
    &:active::after,
    &:focus::after {
        border: 2px solid @pink;
        outline: none;
    }
    &::before {
        position: absolute;
        z-index: 2;
        top: 5px;
        left: 9px;

        display: none;

        width: 40px;
        height: 40px;

        content: "";

        background-image: url(../img/icon-yes-pink.svg);
        background-repeat: no-repeat;

        fill: @pink;
    }
}


#selfi:checked ~ .form__achievements-label--selfi::before,
#beech:checked ~ .form__achievements-label--beech::before,
#chuck:checked ~ .form__achievements-label--chuck::before,
#souvenirs:checked ~ .form__achievements-label--souvenirs::before,
#tower:checked ~ .form__achievements-label--tower::before,
#hotel:checked ~ .form__achievements-label--hotel::before {
    display: block;
}
.form__contacts-fieldset {
    margin: 0 auto;
    padding: 0;

    border: 0;
}
.form__contacts-list {
    display: flex;
    flex-direction: column;

    width: 280px;
    margin: 0;
    padding: 0;
    padding-top: 32px;
    padding-bottom: 43px;

    list-style: none;
}

.form__contacts-title {
    display: none;
}

.form__contacts-item:nth-of-type(2) {
    margin-top: 16px;
}
.form__area--contacts {
    width: 276px;
}

.form__os {
    width: 100%;
    padding-top: 39px;
    padding-bottom: 25px;

    background-color: @bg-grey;
}
.form__os-fieldset {
    padding: 0;

    border: none;
}
.form__os-wrapper {
    display: flex;
    flex-direction: column;

    width: 280px;
    margin: 0 auto;
    padding: 0;

    border: none;
}

.form__os-wrapper input[type="radio"] {
    display: none;
}

.form__os-title {
    font-size: 14px;
    font-weight: normal;
    line-height: 18px;

    width: 230px;
    margin: 0 auto;
    padding-bottom: 40px;

    text-align: center;
    text-transform: uppercase;

    color: @pink;
}

.form__os-label {
    font-weight: normal;

    position: relative;

    min-height: 50px;
    margin-top: 25px;
    padding-left: 69px;

    color: @bg-lightblue;
    &::after {
        position: absolute;
        top: -6px;
        left: 0;

        width: 35px;
        height: 35px;

        content: "";

        border: 2px solid @border-color-gray;
        border-radius: 50%;
        background-color: @bg-white;
    }
    &:hover::after {
        border: 2px solid @bg-lightblue;
    }
    &:active::after {
        border: 2px solid @pink;
    }

    &::before {
        position: absolute;
        z-index: 2;
        top: 6px;
        left: 12px;

        display: none;

        width: 15px;
        height: 15px;

        content: "";

        border-radius: 50%;
        background-color: @pink;
    }
}

.form__os-label:first-of-type {
    margin-top: 0;
}

#ios:checked ~ .form__os-label:first-of-type::before,
#android:checked ~ .form__os-label:nth-of-type(2)::before,
#windows:checked ~ .form__os-label:nth-of-type(3)::before {
    display: block;
}

.form__emotions {
    width: 100%;
}
.form__emotions-fieldset {
    width: 280px;
    margin: 0 auto;
    padding: 0;

    border: none;
}

.form__emotions-wrapper {
    display: flex;
    flex-direction: column;

    width: 280px;
    margin: 0 auto;
    padding: 0;

    border: none;
}
.form__emotions-title {
    font-size: 14px;
    font-weight: normal;
    line-height: 18px;

    width: 230px;
    margin: 0 auto;
    padding-top: 38px;
    padding-bottom: 16px;

    text-align: center;
    text-transform: uppercase;

    color: @pink;
}

.form__emotions-description {
    width: 272px;
    min-height: 193px;
    margin: 0 auto;

    resize: none;

    border: 2px solid @border-color-gray;
}
.form__emotions-description::placeholder {
    line-height: 30px;

    padding-top: 12px;
    padding-left: 17px;

    color: @bg-lightblue;
}

.form__button-wrapper {
    display: flex;
    flex-direction: column;

    width: 280px;
    margin: 0 auto;
    padding-top: 41px;

    align-items: center;
}

.form__must {
    font-size: 14px;

    margin-top: 37px;
    padding-bottom: 2px;

    text-transform: uppercase;

    color: @bg-lightblue;
}
.form__must-star {
    font-size: 14px;

    color: @pink;
}
.form__must-star--hidden {
    display: none;
}

