.slider {
  position: relative;
}
.slider__toggle-hidden {
  display: none;
}
.slider__item {
  display: none;
}
.slider__controls {
  position: relative;
  display: flex;
  width: 57px;
  justify-content: space-between;
  align-items: center;
}
.slider__toggle {
  width: 6px;
  height: 6px;
  cursor: pointer;
  border-radius: 50%;
  background-color: #bec2c7;
}
.slider__toggle:hover {
  background-color: #7e868f;
}
.slider__toggle:active {
  background-color: #e9ebec;
}
#slide-1:checked ~ .slider__list .slider__item:nth-of-type(1) {
  display: flex;
}
#slide-2:checked ~ .slider__list .slider__item:nth-of-type(2) {
  display: flex;
}
#slide-3:checked ~ .slider__list .slider__item:nth-of-type(3) {
  display: flex;
}
#slide-1:checked ~ .slider__controls .slider__toggle:nth-of-type(1)::after {
  position: absolute;
  top: -3px;
  left: -3px;
  width: 6px;
  height: 6px;
  content: "";
  border: 3px solid #283645;
  border-radius: 50%;
  background-color: #ffffff;
}
#slide-2:checked ~ .slider__controls .slider__toggle:nth-of-type(2)::after {
  position: absolute;
  top: -3px;
  left: 22px;
  width: 6px;
  height: 6px;
  content: "";
  border: 3px solid #283645;
  border-radius: 50%;
  background-color: #ffffff;
}
#slide-3:checked ~ .slider__controls .slider__toggle:nth-of-type(3)::after {
  position: absolute;
  top: -3px;
  left: 48px;
  width: 6px;
  height: 6px;
  content: "";
  border: 3px solid #283645;
  border-radius: 50%;
  background-color: #ffffff;
}
