.main-nav {
  position: relative;
  width: 320px;
  min-height: 51px;
  margin: 0 auto;
}
.main-nav__toggle {
  position: absolute;
  z-index: 2;
  top: 11px;
  right: 10px;
  display: block;
  width: 70px;
  height: 50px;
  cursor: pointer;
  border: none;
  background-color: rgba(29, 39, 49, 0);
}
.main-nav--closed .main-nav__toggle::before {
  position: absolute;
  top: 8px;
  right: 10px;
  width: 50px;
  height: 4px;
  content: "";
  background-color: #fff;
  box-shadow: 0 10px 0 0 #fff, 0 20px 0 0 #fff;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: #e06989;
  box-shadow: 0 10px 0 0 #e06989, 0 20px 0 0 #e06989;
}
.main-nav--closed .site-list__item:nth-of-type(1) a {
  border-bottom: 1px solid rgba(29, 39, 49, 0.3);
  background-color: rgba(0, 0, 0, 0.3);
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  position: absolute;
  top: 20px;
  right: 6px;
  width: 30px;
  height: 5px;
  content: "";
  background-color: #fff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #d22856;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: #e06989;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 959px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--closed .site-list__item--active-first {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
}
@media (min-width: 660px) {
  .main-nav {
    width: auto;
    margin: 0 auto;
  }
  .main-nav__wrapper {
    width: auto;
  }
  .main-nav__toggle {
    top: 36px;
  }
}
@media (min-width: 960px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav {
    width: auto;
    margin: 0 auto;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .main-nav__wrapper {
    width: auto;
    margin: 0 auto;
  }
  .main-nav--closed .site-list__item:nth-of-type(1) a {
    background-color: rgba(0, 0, 0, 0);
  }
}
