.download {
    min-width: 320px;

    background-color: @bg-grey;
}
.download__title-wrapper {
    background-color: @bg-white;
}
.download__title {
    font-size: 14px;
    font-weight: normal;
    line-height: 30px;

    display: block;

    width: 280px;
    margin: 0 auto;
    padding-top: 23px;
    padding-bottom: 41px;

    text-align: center;

    color: @bg-lightblue;
}

.download__wrapper {
    display: flex;
    flex-direction: column;

    padding-top: 20px;
    padding-bottom: 41px;

    text-align: center;
}
.download__container {
    display: flex;
    flex-direction: column;

    align-items: center;
}

.download__list {
    display: flex;

    width: 206px;
    margin: 0;
    margin-top: 33px;
    padding: 0;

    list-style: none;

    justify-content: space-between;
}
.download__item {
    position: relative;

    width: 50px;
    height: 50px;
}
.download__item:nth-of-type(1) .download__item-img {
    position: absolute;
    top: 0;
    left: 0;
}
.download__item:nth-of-type(2) .download__item-img {
    position: absolute;
    top: 3px;
    left: 1px;
}
.download__item:nth-of-type(3) .download__item-img {
    position: absolute;
    top: 8px;
    left: 11px;
}

.download__range-list {
    display: flex;
    flex-direction: column;
}
.download__range-item {
    position: relative;

    width: 240px;
    height: 50px;
    margin-top: 10px;
}

.download__range-item::before {
    position: absolute;
    top: 25px;
    left: 0;

    width: 240px;
    height: 2px;

    content: "";

    background-color: @bg-lightblue;
}
.download__range-item::after {
    position: absolute;
    z-index: 2;

    width: 10px;
    height: 10px;

    content: "";
    cursor: pointer;

    border: 2px solid @pink;
    border-radius: 50%;
    background-color: @bg-grey;
}
.download__range-item:active::after {
    background-color: @pink;
}

.download__range-item:first-of-type::after {
    top: 19px;
    left: 191px;
}
.download__range-item:nth-of-type(2)::after {
    top: 19px;
    left: 31px;
}
.download__range-item:nth-of-type(3)::after {
    top: 19px;
    left: 121px;
}

.download__container input {
    display: none;
}
.download__range-item {
    display: none;
}

#crop:checked ~ .download__range-list .download__range-item:nth-of-type(1) {
    display: block;
}
#fill:checked ~ .download__range-list .download__range-item:nth-of-type(2) {
    display: block;
}
#contrast:checked ~ .download__range-list .download__range-item:nth-of-type(3) {
    display: block;
}
#crop:checked ~ .download__list .download__item:nth-of-type(1) .download__item-img{
    opacity: 1;

    fill: @pink;
}
#fill:checked ~ .download__list .download__item:nth-of-type(2) .download__item-img{
    opacity: 1;

    fill: @pink;
}
#contrast:checked ~ .download__list .download__item:nth-of-type(3) .download__item-img{
    opacity: 1;

    fill: @pink;
}
.download__item-img {
    cursor: pointer;

    opacity: .3;
}
.download__item-img:hover {
    opacity: .5;
}
.download__button-wrapper {
    display: flex;
    flex-direction: column;
}

.download__button {
    width: 244px;
    margin-top: 50px;

    color: @pink;
    border-color: @pink;

    &:hover,
    &:focus {
        color: @bg-white;
        background-color: @pink;
    }

    &:active {
        color: @pink-active;
        background-color: @pink;
    }
}
.download__button-cancel {
    width: 244px;
    margin-top: 13px;

    color: rgb(221, 223, 224);
    border-color: rgb(221, 223, 224);
    background-color: inherit;

    &:hover,
    &:focus {
        color: rgb(181, 185, 190);
        border-color: rgb(181, 185, 190);
    }
    &:active {
        color: rgb(203, 206, 210);
        border-color: rgb(181, 185, 190);
        background-color: rgb(181, 185, 190);
    }
}

@media (min-width: @tablet-width) {
    .download__item-img {
        display: none;
    }
    .download__title {
        font-size: 18px;
        font-weight: normal;
        line-height: 30px;

        width: 500px;
        padding: 65px 0 68px 0;
    }
    .download__wrapper {
        display: flex;
        flex-direction: row;

        width: 620px;
        margin: 0 auto;
        padding-top: 58px;
        padding-bottom: 46px;

        justify-content: space-between;
    }
    .download__range-item:nth-of-type(2),
    .download__range-item:nth-of-type(3) {
        display: flex;
    }

    .download__container {
        display: flex;
        flex-direction: row;

        width: 270px;
        margin-top: 8px;

        flex-wrap: wrap;
        align-items: flex-start;
    }

    .download__list {
        display: flex;
        flex-direction: column;

        width: 60px;
        margin: 0;
    }
    .download__item {
        width: 50px;
        height: 70px;
    }
    .download__item:first-of-type::before {
        position: absolute;
        top: 0;
        left: -2px;

        width: 24px;
        height: 24px;

        content: "";

        background-image: url(../img/icon-editor-crop.svg);
        background-repeat: no-repeat;
        background-size: 24px;
    }
    .download__item:nth-of-type(2)::before {
        position: absolute;
        top: 5px;
        left: -2px;

        width: 25px;
        height: 25px;

        content: "";

        background-image: url(../img/icon-editor-fill.svg);
        background-repeat: no-repeat;
        background-size: 24px;
    }
    .download__item:nth-of-type(3)::before {
        position: absolute;
        top: 7px;
        left: 3px;

        width: 16px;
        height: 16px;

        content: "";

        background-image: url(../img/icon-editor-contrast.svg);
        background-repeat: no-repeat;
        background-size: 16px;
    }

    .download__range-item {
        width: 203px;
        height: 70px;
        margin-top: 0;
    }
    .download__range-item::before {
        top: 11px;
        left: -10px;

        width: 202px;
    }
    .download__range-item:nth-of-type(2)::before,
    .download__range-item:nth-of-type(3)::before {
        top: 14px;
    }
    .download__range-item:first-of-type::after {
        top: 5px;
        left: 136px;
    }
    .download__range-item:nth-of-type(2)::after {
        top: 8px;
        left: 15px;
    }
    .download__range-item:nth-of-type(3)::after {
        top: 8px;
        left: 86px;
    }
    .download__button-wrapper {
        margin-top: -7px;
    }

    .download__button,
    .download__button-cancel {
        width: 223px;
        padding-right: 17px;
        padding-left: 10px;

        align-self: center;
    }
    .download__button-cancel {
        margin-top: 27px;
    }
}

@media (min-width: @desktop-width) {
    .download__title {
        padding: 57px 0 65px 0;
    }
    .download__wrapper {
        width: 780px;
        padding-bottom: 49px;
    }
    .download__container {
        width: 390px;
    }

    .download__item {
        width: 50px;
        height: 70px;
    }
    .download__item:first-of-type::before {
        top: -12px;
        left: -2px;

        width: 48px;
        height: 48px;

        background-size: 48px;
    }
    .download__item:nth-of-type(2)::before {
        top: -7px;
        left: 0;

        width: 48px;
        height: 48px;

        background-size: 48px;
    }
    .download__item:nth-of-type(3)::before {
        top: -1px;
        left: 6px;

        width: 32px;
        height: 32px;

        background-size: 32px;
    }
    .download__range-item {
        width: 203px;
        height: 70px;
        margin-top: 0;
    }
    .download__range-item::before {
        top: 11px;
        left: 32px;

        width: 288px;
    }
    .download__range-item:nth-of-type(2)::before,
    .download__range-item:nth-of-type(3)::before {
        top: 14px;
    }
    .download__range-item:first-of-type::after {
        top: 5px;
        left: 104px;
    }
    .download__range-item:nth-of-type(2)::after {
        top: 8px;
        left: 280px;
    }
    .download__range-item:nth-of-type(3)::after {
        top: 8px;
        left: 192px;
    }
    .download__button-wrapper {
        display: flex;
        flex-direction: row;

        margin-top: 120px;
    }
    .download__button,
    .download__button-cancel {
        width: 148px;
        margin: 0;
        padding-right: 13px;
        padding-left: 17px;

        align-self: flex-start;
    }
    .download__button-cancel {
        margin: 0;
    }
    .download__button {
        margin-right: 40px;
    }
}
